@import "@/styles/main.scss";





























































































































































































































    .contact-left-section{
          display: flex;
          flex-direction: column;
          width: 50%;
          height: 600px;
          max-height: 600px;
          justify-content: space-between;
          align-items: flex-start;
          padding-top: 110px;
          padding-left: 49px;
          z-index: 10;
          position: relative !important;

          .map-link{
            text-decoration: none !important;

            :hover{
              text-decoration: none !important;
            }
          }

          .contact-tooltip{
            font-size: 0.9rem;
            cursor: pointer;
            background-color: rgba($color: #222222, $alpha: 0.9);
            border-radius: 8px;
            display: flex;
            justify-content: space-between;
            align-items: center !important;
            padding: 3px 12px;
            color: #999999;
            position: relative;
            display: none;

            svg{
              color: #999999;
              width: 10px !important;
              font-size: 1.2rem !important;
              padding-top: 0px;
              margin-left: 5px;

            }

            &:hover{
              background-color: rgba($color: #222222, $alpha: 0.9);
              color: #ffffff;

              svg{
                color: #ffffff;
              }

            }
          }

          .contact-txt{
            font-weight: 500;
            font-size: 3.5rem;
            z-index: 10;
          }
      }

      .contact-right-section{
          width: 50%;
          height: 600px;
          max-height: 600px;
          justify-content: flex-end;
          align-items: flex-end;
          padding-right: 40px;

          .modal-contact-page{
            z-index: 10;
            background-color: #ffffff;
            border-radius: 40px;
            padding: 0 0 0 10px;
            cursor: default;
            width: 380px;
            height: 530px;
            margin-bottom: -40px;
            justify-content: space-between;
            align-items: flex-start;
            -webkit-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.65);
            -moz-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.65);
            box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.65);

            p.form-error-txt{
              padding: 0;
              margin-top: -20px;
              font-size: 0.8rem;
              font-size: 400;
              color: #d9534f;
              // margin-top: 5px;
              text-align: left;
            }

            .contact-page-form{

              height: 100%;
              justify-content: space-between;
              padding: 30px 20px 30px;

              input{
                width: 100%;
                border: none;
                border-radius: 0;
                border-top-left-radius: 10px;
                border-top-right-radius: 10px;
                -webkit-appearance: none;
                outline: none;
                border-bottom: 1px solid rgba($color: #949494, $alpha: 0.6);
                background-color: transparent;
                margin-bottom: 25px;
                animation-duration: 0.3s;
                padding: 25px 12px 6px 10px;
                font-size: 18px;

                  &:focus{
                      outline: none;

                      &::placeholder{
                          color: #949494 !important;
                          -webkit-transform: translateY(-10px) translateX(10px);
                          -moz-transform: translateY(-10px) translateX(10px);
                          transform: translateY(-10px) translateX(10px);
                        }
                  }

                  &::-webkit-input-placeholder{
                      -webkit-transform: translateY(-10px);
                      -moz-transform: translateY(-10px);
                      transform: translateY(-10px);
                      transition: all 0.3s ease;
                      color: #949494 ;
                      font-weight: 500 ;
                      font-size: 1rem;
                  }

                  &::-ms-input-placeholder{
                      -webkit-transform: translateY(-10px);
                      -moz-transform: translateY(-10px);
                      transform: translateY(-10px);
                      transition: all 0.3s ease;
                      color: #949494 !important;
                      font-weight: 500 !important;
                      font-size: 1rem;
                  }
              }

              label{
                  width: 100%;
                  text-align: left;
                  margin-bottom: 15px;
                  padding-left: 10px;
                  color: #949494 ;
                  font-size: 0.9rem;

              }

              textarea{
                  width: 100%;
                  border: none;
                  resize: none;
                  -webkit-appearance: none;
                  outline: none;
                  border-radius: 15px;
                  background-color: #F8F8F8;
                  margin-bottom: 0px;
                  animation-duration: 0.3s;
                  padding: 20px 20px 10px 10px;
                  font-size: 16px;
                  overflow: visible ;

                  &:focus{
                      outline: none;
                      // animation-name: pulse;
                      // animation-duration: 0.3s;
                      &::placeholder{
                          color: #949494 !important;
                          -webkit-transform: translateY(-15px) translateX(10px);
                          -moz-transform: translateY(-15px) translateX(10px);
                          transform: translateY(-15px) translateX(10px);
                        }
                  }

                  &::-webkit-input-placeholder{
                      z-index: 100000;
                      -webkit-transform: translateY(-15px);
                      -moz-transform: translateY(-15px);
                      transform: translateY(-15px);
                      transition: all 0.3s ease;
                      color: #949494 ;
                      font-weight: 500 ;
                      font-size: 1rem;
                  }

                  &::-ms-input-placeholder{
                      z-index: 100000;

                      -webkit-transform: translateY(-15px);
                      -moz-transform: translateY(-15px);
                      transform: translateY(-15px);
                      transition: all 0.3s ease;
                      color: #949494 !important;
                      font-weight: 500 !important;
                      font-size: 1rem;
                  }
              }

              .contact-form-buttons{
                justify-content: center;
                align-items: center;
                margin-top: 25px;

                .send-message{
                  display: flex;
                  align-self: flex-end !important;
                  background: linear-gradient(to right, #2B92E1 , #68E3F1);
                  justify-content: space-between;
                  align-items: center;
                  width: 180px;
                  height: 50px;
                  background-color: transparent;
                  color: rgba($color: #ffffff, $alpha: 0.9  );
                  font-weight: 900;
                  text-transform: uppercase;
                  letter-spacing: 0.08rem;
                  word-spacing: 0.2rem;
                  font-size: 0.88rem;
                  border: none;
                  padding: 0 20px 0 20px;
                  outline: none;
                  border-radius: 40px;
                  transition: all 0.2s ease;
                  box-shadow: 0 10px 10px 2px rgba($color: #2B92E1, $alpha: .20), 0 3px 3px 0 rgba($color: #2B92E1, $alpha: .06);


                  &:hover{
                    box-shadow: none;
                    color: #ffffff !important;
                  }

                    .loading{
                      color: #ffffff;
                      font-size: 2rem !important;
                      animation:
                                loading-rotate 1s infinite both;
                                // loading-bg 1.5s infinite both;

                      @keyframes loading-rotate {
                        0% {
                          transform: rotateZ(0deg);
                        }
                        100% {
                          transform: rotateZ(360deg);
                        }
                      }

                      @keyframes loading-bg {
                        0% {
                          color: #ffffff;
                        }
                        100% {
                          color: #000000;
                        }
                      }
                  }
                }

              }
            }
          }
      }


  @media screen and (min-width:1052px) and (max-width: 1165px ){

    .contact-left-section{
      padding-top: 10% !important;
      height: auto !important;

    }

    .contact-right-section{
      height: auto !important;
      padding-top: 10% !important;


    }

  }

  @media screen and (min-width:668px) and (max-width: 1051px ){

      .contact-left-section{

          height: 120% !important;

      }

      .contact-right-section{

        height: 600px !important;
        // padding-top: 800px !important;
        // .modal-contact-page{

        //     width: 300px !important;
        //     height: 430px !important;

        // }
      }
  }


@media only screen
  and (min-device-width: 412px)
  and (max-device-width: 736px)
  and (-webkit-min-device-pixel-ratio: 3) {

    .contact-right-left-container{

      flex-direction: column !important;
      align-items: flex-start !important;
      justify-content: space-between !important;

    }

    .contact-left-section{
      flex-direction: column-reverse !important;
      padding-top: 20px !important;
      padding-left: 28px !important;
      padding-right: 30px !important;
      width: 100% !important;
      align-items: flex-start !important;
      z-index: 10;
      position: absolute !important;
      top: 0;

      p.contact-txt{
        position: absolute;
        top: 70px;
        font-size: 2.2rem !important;
      }

      .contact-tooltip{
        display: flex !important;
        top: 135px;
        position: absolute;
      }
    }

    .contact-right-section{
      width: 100% !important;
      display: flex;
      justify-content: flex-start !important;
      padding-left: 30px !important;
      padding-right: 30px !important;
      position: absolute !important;
      top: 0px;


      .contact-page-form{
        width: 100% !important;

      }

      .modal-contact-page{
          width: 100% !important;
          padding-bottom: 10px !important;
          height: 450px !important;


          label{
            margin-bottom: 15px !important;
          }

          .contact-page-form{
                width: 100% !important;
                height: 430px !important;
                padding: 25px 20px 0px !important;
          }
      }
    }
}
@media only screen
  and (min-device-width: 320px)
  and (max-device-width: 412px)
  and (-webkit-min-device-pixel-ratio: 2) {

    .contact-right-left-container{
      flex-direction: column !important;
      align-items: flex-start !important;
      justify-content: space-between !important;

    }

    .contact-left-section{
      flex-direction: column-reverse !important;
      padding-top: 20px !important;
      padding-left: 30px !important;
      padding-right: 30px !important;
      width: 100% !important;
      align-items: flex-start !important;
      z-index: 10;
      position: absolute !important;
      top: 30px;

      p.contact-txt{
        position: absolute;
        top: 50px;
        font-size: 2rem !important;
      }

      .contact-tooltip{
        position: absolute;
        top: 105px;
        display: flex !important;
      }
    }

    .contact-right-section{
      width: 100% !important;
      display: flex;
      justify-content: flex-start !important;
      padding-left: 30px !important;
      padding-right: 30px !important;
      position: absolute !important;
      top: 0px;

      .contact-page-form{
        width: 100% !important;

      }
      .modal-contact-page{
          width: 100% !important;
          padding-bottom: 10px !important;
          height: 450px !important;


          label{
            margin-bottom: 15px !important;
          }

          .contact-page-form{
                width: 100% !important;
                height: 430px !important;
                padding: 25px 20px 0px !important;
          }
      }
    }
}



@media all and (max-width: 400px ){

  .contact-left-section{

      p.contact-txt{
        font-size: 2rem !important;
      }
  }

  .contact-right-section{
      .form-error-txt{
          font-size: 0.7rem !important;
      }
  }

}
